.intro {
  display: flex;
  height: 77vh;
  margin-top: 6rem;

}

.intro_left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  gap: 2rem;

}

.intro_name {
  display: flex;
  flex-direction: column;
}

.intro_name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 2rem;
}
.intro_name > :nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 2.5rem;
}

.intro_name > :nth-child(3) {
  color: var(--grey);
  font-size: 14px;
  font-weight: 100;
  margin-top: 10px;
}

.intro_hire {
  width: 7rem;
  height: 3rem;
}

.intro_icons {
  display: flex;

  gap: 0rem;
}

.intro_icons > * {
  transform: scale(0.4);
}

.intro_icons > * {
  cursor: pointer;
}

.intro_right {
  flex: 1;
  position: relative;
}
.intro_right > * {
  flex: 1;
  position: absolute;
}

.my_pic {
  position: absolute;
  width: 50%;
  left: 20%;
  border-radius: 50%;
}
.intro_right > :nth-child(2) {
  transform: scale(0.5);

  top: -23%;
  left: -18%;

  border-radius: 50%;

  padding: 0;
}

.blur{
    position: absolute;

    width: 22rem;
    height: 14rem;
    border-radius: 50%;

    background:#edd0ff;
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}


@media all and (min-width:200px ) and (max-width:700px) {
    
  .intro_left{

  }
  .intro{
    display: grid;
    grid-template-columns: repeat(1 , 1fr);
    gap: 4rem;
  }
  .About{
margin-top: 200px;


  }
}