.floatingDiv{
    justify-content: space-around;
    display: flex;
    align-items: center;
  border-radius: 17px;
    background: white;
  padding: 0px 20px 0px 0px;
    box-shadow: var(--boxShadow);
    height: 4.5rem;
}

.floatingDiv>img{transform: scale(0.4);}

.floatingDiv>span{
    font-family: sans-serif;

    font-size: 14px;
}